<template>
   <div class='frame'>
      <component :is='(layoutData.value || "regular") + "-layout"'></component>
   </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  name: 'frame',
  data: function () {
    return {
    }
  },
  computed: {
    ...mapGetters({
      layoutData: 'sysLayoutName/data'
    })
  },
  mounted: function () {
    // this.$route vuex 
  },
  methods: {
  },
  components: {
    RegularLayout: () => import('./regularLayout'),
    NoLayout: () => import('./noLayout')
  }
}
</script>
<style>
.frame {
  height: 100%;
  width: 100%;
}
</style>



