const table = {
  props: {
    // 
    headers: {
      default: function () {
        return []
      },
      type: Array,
      required: true
    },
    // 
    maxHeight: {
      default: '-1',
      type: String
    },
    // 
    border: {
      default: true,
      type: Boolean
    },
    // 
    highlight: {
      default: true,
      type: Boolean
    },
    // 
    stripe: {
      default: true,
      type: Boolean
    },
    // 
    showHeader: {
      default: true,
      type: Boolean
    },
    // 
    select: {
      // 
      default: false,
      type: Boolean
    },
    // 
    index: {
      // 
      default: false,
      type: Boolean
    },
    // 
    selectFun: {
      default: function () {
        return true
      },
      type: Function
    },
    // 
    currentRowKey: {
      type: String || Number,
      default: null
    }
  },
  data: function () {
    return {}
  },
  computed: {},
  mounted: function () {},
  methods: {
    // 
    selectItem (selection, row) {
      this.$emit('selectChange', {row, selection})
    },
    // 
    selectAll (selection) {
      this.$emit('selectChange', {selection})
    },
    // 
    selectionChange (selection) {
      this.$emit('selectChange', {selection})
    },
    // 
    sortChange (column, prop, order) {
    },
    // 
    cellClick (row, column, cell, event) {},
    // 
    renderHeader (h, { column, $index }) {
      return <span>{column.label}</span>
    }
  },
  watch: {},
  components: {}
}

export default table



